<template>
  <div>
    <div class="video-background">
      <div class="video-foreground">
        <iframe :src="'https://www.youtube.com/embed/' + movie.trailer + '?controls=0&showinfo=0&rel=0&autoplay=1&mute=1&loop=1&playlist=' + movie.trailer" frameborder="0" allowfullscreen></iframe>
      </div>
    </div>
    <div class="top">
      <div class="container">
        <div class="row">
          <div class="col-6">
            <GoBack />
          </div>
          <div class="col-6">
            <div class="favorites-container">
              <a @click="setVote(movie.id)" class="btn" :class="moviesVotes[movie.id] ? 'unfavorite' : 'favorite'" data-toggle="tooltip" data-placement="bottom" title="Favorites"><i class="fa fa-heart"></i></a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="movie-details">
      <div class="container">
        <div class="row details-container">
          <div class="col-sm-12 col-md-4">
            <div class="poster-container">
              <div class="details-poster" :style="'background-image: url(https://image.tmdb.org/t/p/w600_and_h900_bestv2' + movie.poster_path + ')'"></div>
            </div>
          </div>
          <div class="col-sm-12 col-md-8">
            <div class="info-container">
              <h2>{{movie.title}}</h2>
              <p class="tagline">{{movie.tagline}}</p>
              <p class="details-info"><i class="fas fa-star"></i>{{movie.vote_average}}/10</p>
              <p class="details-info"><i class="fas fa-circle separator"></i></p>
              <p class="details-info">{{movie.release_date}}</p>
              <p class="details-info"><i class="fas fa-circle separator"></i></p>
              <p class="details-info">{{movie.runtime}} mins</p>
              <p class="details-info"><i class="fas fa-circle separator"></i></p>
              <p class="details-info">{{movie.genres}}</p>
              <p class="overview">{{movie.overview}}</p>
            </div>
            <div class="streaming-container">
              <router-link :to="{ name: 'movieStream', params: { imdb_id: movie.imdb_id } }">
                <button class="btn btn-primary watch-button"><i class="fas fa-play"></i>Play now</button>
              </router-link>
              <a class="btn btn-outline-secondary watch-button" :href="'magnet:?xt=urn:btih:' + movie.torrent + '&tr=udp%3A%2F%2Fglotorrents.pw%3A6969%2Fannounce&tr=udp%3A%2F%2Ftracker.openbittorrent.com%3A80&tr=udp%3A%2F%2Ftracker.coppersurfer.tk%3A6969&tr=udp%3A%2F%2Fp4p.arenabg.ch%3A1337&tr=udp%3A%2F%2Ftracker.internetwarriors.net%3A1337tracker.openbittorrent.com%3A80&tr=wss%3A%2F%2Ftracker.btorrent.xyz&tr=wss%3A%2F%2Ftracker.fastcast.nz&tr=wss%3A%2F%2Ftracker.openwebtorrent.com'"><i class="fas fa-magnet"></i>Download torrent</a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>
<script>
import list from "@/list";
import GoBack from "@/components/GoBack";
import Footer from "@/components/Footer";
export default {
  name: "movieDetails",
  components: {
    GoBack,
    Footer
  },
  data() {
    return {
      movies: list.movies,
      moviesVotes: {}
    };
  },
  props: {
    imdb_id: {
      type: String,
      required: true
    }
  },
  mounted() {
    let appScript = document.createElement("script");
    appScript.setAttribute("src", "/js/script.js");
    document.head.appendChild(appScript);
    this.setVotesFromPersistence();
  },
  methods: {
  /**
   * set vote on persistence(localStorage) if not exists and
   * update the object moviesClass with the current vote
   *
   * @param {string} movieId
   */
    setVote(movieId) {
      if (localStorage.getItem(movieId)) {
        localStorage.removeItem(movieId);
        this.moviesVotes[movieId] = null;
      } else {
        localStorage.setItem(movieId, true);
        this.moviesVotes[movieId] = true;
      }
    },
    setVotesFromPersistence() {
      this.moviesVotes = this.movies.reduce((acc, item) => {
        acc[item.id] = localStorage.getItem(item.id);
        return acc;
      }, {});
    }
  },
  computed: {
    movie() {
      return list.movies.find(movie => movie.imdb_id === this.imdb_id);
    }
  }
};
</script>
